import React,{Component} from 'react'


import Form from 'react-bootstrap/Form'
import Button from 'react-bootstrap/Button'

import Hero from '../components/Hero'
import Content from '../components/Content'
import Axios from 'axios'

class ContactPage extends Component{

     constructor(props){
        super(props);
        this.state = {
            firstName:'',
            lastName:'',
            email:'',
            objet:'',
            message:'',
            disabled:false,
            emailSent:null
        }
    }

    handleChange = (e) =>{

        const target = e.target;
        const value = target.type === 'checkbox' ? target.checked : target.value;
        const name = target.name;

        this.setState({
            [name]:value
        })
    }

    handleSubmit = (e) =>{
        e.preventDefault();

        this.setState({
            disabled:true
        });

        Axios.post('http://185.31.40.18:8100/api/email',this.state)
            .then(res => {
                if(res.data.success){
                    console.log("oui");
                    this.setState({
                        disabled:false,
                        emailSent:true
                    });
                }else{
                    console.log("non");
                    this.setState({
                        disabled:false,
                        emailSent:false
                    });
                }
                
            })
            .catch(err=>{
                console.log("test2")
                this.setState({
                    disabled:false,
                    emailSent:false
                });
            })
    }

    render(){
        return(
            <div>
                <Hero title={this.props.title}/>

                
                <Content>
                    <Form onSubmit={this.handleSubmit}>
                        <Form.Group>
                            <Form.Label htmlFor="prenom">Prenom</Form.Label>
                            <Form.Control id="prenom" name="firstName" type="text" value={this.state.firstName} onChange={this.handleChange}/>
                        </Form.Group>


                        <Form.Group>
                            <Form.Label htmlFor="nom">Nom</Form.Label>
                            <Form.Control id="nom" name="lastName" type="text" value={this.state.lastName} onChange={this.handleChange}/>
                        </Form.Group>
                        
                        
                        <Form.Group>
                            <Form.Label htmlFor="email">Email</Form.Label>
                            <Form.Control id="email" name="email" type="email" value={this.state.email} onChange={this.handleChange}/>
                        </Form.Group>
                        
                        <Form.Group>
                            <Form.Label htmlFor="objet">Objet</Form.Label>
                            <Form.Control id="objet" name="objet" type="text" value={this.state.objet} onChange={this.handleChange}/>
                        </Form.Group>
                        
                        <Form.Group>
                            <Form.Label htmlFor="message">Message</Form.Label>
                            <Form.Control id="message" name="message" as="textarea" rows="3" value={this.state.message} onChange={this.handleChange}/>
                        </Form.Group>

                        <Button className="d-inline-block" variant="primary" type="submit" disabled={this.state.disabled}>
                            Envoyer
                        </Button>


                         {this.state.emailSent === true && <p className="d-inline success-msg">Message envoyé</p>}
                         {this.state.emailSent === false && <p className="d-inline err-msg">Message non envoyé</p>}

                    </Form>
                </Content>
            </div> 
    
        )
    }
    


} 

export default ContactPage;