import React,{Component} from 'react'

import battle1 from '../assets/images/battlefield_PNG41.png';
import battle2 from '../assets/images/battlefield_PNG55.png';
import iron from '../assets/images/unnamed.png';
// import paysage1 from '../assets/images/paysage1.jpg';
// import paysage2 from '../assets/images/paysage2.png';
// import paysage3 from '../assets/images/paysage3.jpeg';


import conquest from '../assets/images/Conquest.png';
import IUT from '../assets/images/IUT.png';
import HardWare from '../assets/images/E-Hardware.png';

import Card from './Card'
import Carousel from 'react-bootstrap/Carousel'
 import Container from 'react-bootstrap/Container';
 import Row from 'react-bootstrap/Row';


class Carousele extends Component{

    constructor(props){
        super(props);
        this.state = {
            items : [
                {
                    id : 0,
                    title : 'titre',
                    subTitle:'soustitre',
                    imgSrc : iron,
                    link:'www.google.com',
                    selected:false,
                },

                {
                    id : 1,
                    title : 'titre',
                    subTitle:'soustitre',
                    imgSrc : battle1,
                    link:'www.google.com',
                    selected:false,
                },

                {
                    id : 2,
                    title : 'titre',
                    subTitle:'soustitre',
                    imgSrc :battle2,
                    link:'www.google.com',
                    selected:false,
                },
                {
                    id : 3,
                    title : 'titre',
                    subTitle:'soustitre',
                    imgSrc :conquest,
                    link:'www.google.com',
                    selected:false,
                },
                {
                    id : 4,
                    title : 'titre',
                    subTitle:'soustitre',
                    imgSrc :IUT,
                    link:'www.google.com',
                    selected:false,
                },
                {
                    id : 5,
                    title : 'titre',
                    subTitle:'soustitre',
                    imgSrc :HardWare,
                    link:'www.google.com',
                    selected:false,
                },
            ]
        }
    }

    handleCardDisplay = () =>{
        let items = [...this.state.items];
        let count = 1;
        for (let i = 0; i < items.length; i++) {
            if(count > 3 ){
                items[i].display="none";
            }
            count++;
        }
    }

    handleCardClick = (id,card) =>{
        console.log(id)
        let items = [...this.state.items];
        console.log([items])
        items[id].selected = items[id].selected ? false :true;
        items.forEach(item=>{
            if(item.id !== id){
                item.selected = false;
            }
        });

        this.setState({
            items
        })
    }


    makeItems = (items) =>{
        return items.map(item =>{
            return<Card item={item} click={(e => this.handleCardClick(item.id,e))} key={item.id} />
        })
    }

    makeCarouselItem = (items) =>{
        return items.map((item,index)=>{
            return (
            <Carousel.Item key={item.id}>
                 
                 <Card item={item} click={(e => this.handleCardClick(item.id,e)) } key={item.id}/>
                
                {(item.selected===false)? (<Carousel.Caption>
                    <h3 style={{color:"gray"}}>{index+1} slide label</h3>
                    <p style={{color:"gray"}} >Nulla vitae elit libero, a pharetra augue mollis interdum.</p>
                 </Carousel.Caption>) : (
                 <Carousel.Caption>

                 </Carousel.Caption>
                 ) }

                 
                 
            </Carousel.Item>
            )
        })
    }
    

    render(){
        return(
            // <Container fluid={true}>
            //     <Row className="justify-content-around">
            //         {this.makeItems(this.state.items)}
            //     </Row>
            // </Container>
            <Container s={4} className="bgc-black" fluid={true} >
                <Row s={4} className="justify-content-around ">
                <Carousel keyboard={false} slide={false} fade={true}  className="justify-content-around ">
                    {this.makeCarouselItem(this.state.items)}
                </Carousel>
                </Row>
                
            </Container>
            
        )
    }



}

export default Carousele;