import React,{Component} from 'react';
import {BrowserRouter as Router,Route,Link} from 'react-router-dom';
import Container from 'react-bootstrap/Container';
import NavBar from 'react-bootstrap/Navbar';
import Nav from 'react-bootstrap/Nav';
import './App.css';



import Footer from './components/Footer'
import HomePage from './pages/HomePage';
import ContactPage from './pages/ContactPage';
import AboutPage from './pages/AboutPage';

class App extends Component{

constructor(props){
  super(props);
  this.state = {
    title : 'Davio Viator',
    headerLinks:[
      {title : 'Home',path:'/'},
      {title : 'About',path:'/about'},
      {title : 'Contact',path:'/contact'}
    ],
    home : {
      title :'Réalisation',
      subTitle : 'Projets',
      description :'Voici les projets que j\'ai réalisé pendant mes étude a l\'IUT'
    },
    about : {
      title :'About me'
    },
    contact : {
      title :'Let\'s Talk'
    }
  }
}



  render(){
    return (
      <Router>
        <Container className="p-0" fluid={true}>


          <NavBar className="border-bottom" bg="transparent" expand="lg">
            <NavBar.Brand>Davio Viator</NavBar.Brand>

            <NavBar.Toggle className="boarder-0" aria-controls="navbar-toggle"/>
            <NavBar.Collapse id="navbar-toggle">
              <Nav className="ml-auto">
                <Link className="nav-link" to="/">Home</Link>
                <Link className="nav-link" to="/about">About</Link>
                <Link className="nav-link" to="/contact">Contact</Link>

              

              </Nav>

            </NavBar.Collapse>
          </NavBar>
            <Route path="/" exact render={()=> <HomePage title={this.state.home.title} subTitle={this.state.home.subTitle} description={this.state.home.description}  />}/>
            <Route path="/about" exact render={() =><AboutPage title={this.state.about.title} />}/>
            <Route path="/contact" exact render={() =><ContactPage title={this.state.contact.title} />}/>

        <Footer/>

        </Container>
      </Router>
    );
  }
}

export default App;
