import React from 'react'


import CardInfo from './CardInfo'


const Card = (props) =>{

    return(
        <div className="d-inline-block  d-card" onClick={(e) => props.click(props.item)}>
            <img  className="d-card-image" src={props.item.imgSrc} alt={props.item.imgSrc } ></img>
            {props.item.selected && <CardInfo className="justify-content-between" title={props.item.title} subTitle={props.item.subTitle} link={props.item.link} />}
        </div>    
    )

}

export default Card;